import React, { useState, useEffect, useContext } from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import ScanditBarcodeScanner from 'scandit-sdk-react';

import { ScanningBy } from '@scandit/react-toolkit';

import {
  Settings,
  getScanSettings,
  ResultsContext,
} from '../shared';

import { listOfProducts } from '../products/Products';

export function ScanPage() {
  const scanSettings = getScanSettings();

  const [ready, setReady] = useState();
  const [result, setResult] = useState();
  // const [scanError, setScanError] = useState(null);

  const history = useHistory();
  const { productData, setProductData } = useContext(ResultsContext);

  useEffect(() => {
    const barcode = result && result.barcodes && result.barcodes[0];
    if (!barcode) { return; }

    if (barcode) {
      const correctItem = listOfProducts.filter(product => product.id === barcode.data);
      setProductData(listOfProducts);
      history.push(`/result/${correctItem[0].id}`);
      /* czy tutaj object barcode moge prekazac dalej?
      jakims fetchem czy exportem? np do productsbarcode albo products? */
    }
  }, [result]);

  useEffect(() => {
  }, [productData]);

  const onScan = (scanResult) => {
    console.log('scanner is ready', ready);
    setResult(scanResult);
  };

  const onScanError = err => console.error('Scan error:', err);
  return (
    <ScanContent>
      <ScanditBarcodeScanner
        {...Settings.barcodePicker}
        licenseKey={Settings.licenceKey} // done
        engineLocation={Settings.engineLocation} // done
        onReady={() => setReady(true)} // done
        onScan={onScan}
        onScanError={onScanError}
        onScannerInitialized={console.log}
        scanSettings={scanSettings}
        paused={false}
        accessCamera
        guiStyle="laser" // done
      />
      <ScanningBy />
    </ScanContent>
  );
}

const ScanContent = styled.div`
  .scandit-logo {
    display: none;
  }

  transition: opacity 0.3s;
  opacity: 1;
  width: 100%;
  height: 100%;
`;
