import React, { useContext } from 'react';

import styled from 'styled-components';
import { useParams, Link } from 'react-router-dom';

import CameraAltIcon from '@material-ui/icons/CameraAlt';
import ButtonBase from '@material-ui/core/ButtonBase';

import { AlternativeProducts } from './AlternativeProducts';
import { AdditionalProductInfo } from './AdditionalProductInfo';

import { ResultsContext } from '../shared/context';
import { Colors, Mixins } from '../shared/typography';

export function ResultsPage() {
  const { productData } = useContext(ResultsContext);
  // I cannot undesrstand how exactly params works,
  // when I change id to lets say endPointParam it does not work anymoar,
  // how he know its /result/id right now?
  const { id } = useParams();
  const singleItem = productData.filter(product => product.id.toString() === id);

  const {
    numberOfItems,
    name,
    shippingDays,
    code,
    description,
    image,
    itemType,
    itemCategory,
  } = singleItem[0];

  const alternativeItems = productData.filter(item => item.itemCategory === itemCategory &&
     item.id !== singleItem[0].id);

  return (
    <Container>
      <Header>
        <ScanButton to="/scan-consumer-product">
          {/* had to put CameraAltIcon so I can style it via styled-components
          like this styled(CameraAltIcon)`...` line 110 */}
          <ScanAgainButton>
            <CameraAltIcon />
          </ScanAgainButton>
          <ScanButtonText>Scan again</ScanButtonText>
        </ScanButton>
      </Header>
      <UpperSection>
        <Image src={image} />
        <Info>
          <NormalText>
            {numberOfItems}
            Items in Stock
          </NormalText>
          <Title>{name}</Title>
          <LabelContainer>
            <InnerLabelContainer>
              <Label>Shipping</Label>
              <NormalText>
                {shippingDays}
                bussines days
              </NormalText>
            </InnerLabelContainer>
            <InnerLabelContainer>
              <Label>EAN/UPC</Label>
              <NormalText>{code}</NormalText>
            </InnerLabelContainer>
          </LabelContainer>
        </Info>

      </UpperSection>
      <LowerSection>
        <Label>Product Description</Label>
        <NormalText>{description}</NormalText>
        {itemType === 'consumer' && <AlternativeProducts items={alternativeItems} />}
        {itemType === 'industrial' && <AdditionalProductInfo info={singleItem[0]} />}
      </LowerSection>
      <Footer>
        <ReOrderButton onClick={() => alert(`item ${name} has been re-ordered`)}>
          <LinkButton to="/">
            Re-order Item
          </LinkButton>
        </ReOrderButton>
      </Footer>
    </Container>
  );
}

const Header = styled.div`
  display:flex;
  flex-direction: row;
  border-bottom: 1px solid ${Colors.black}
`;

const Container = styled.div`
  ${Mixins.helvetica};
  padding: 20px 15px 0px 15px;
  display: flex;
  flex-direction: column;
  height: 100%;
  box-sizing: border-box;
`;

const ScanAgainButton = styled(CameraAltIcon)`
  height: 20px;
  width: 20px;
  color: ${Colors.darkGrey};
  justify-content: flex-start;
  margin-right: 5px;
`;

const UpperSection = styled.div`
  padding: 20px 0px 0px 0px;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
`;

const LowerSection = styled.div`
  display:flex;
  flex-direction: column;
  box-sizing: border-box;
  height: 600px;
  margin-bottom: 20px;
`;

const Info = styled.div`
  justify-content: end;
`;

const Image = styled.img`
  width: 120px;
  height: 120px;
  justify-content: start;
  margin-right: 20px;
  border: 1px solid black;
`;

const ScanButton = styled(Link)`
  ${Mixins.helvetica};
  display: flex;
  flex-direction: row;
  text-decoration: none !important;
  margin-bottom: 5px;
  &:visited {
    color: ${Colors.darkGrey};
  }
`;

const LinkButton = styled(Link)`
  ${Mixins.helvetica};
  text-decoration: none !important;
  margin-bottom: 5px;
  &:visited {
    color: ${Colors.white};
  }
`;

const ScanButtonText = styled.p`
  margin: 0;
  font-size: 14px;
  font-weight: bold;
  line-height: 22px;
`;

const NormalText = styled.p`
  margin: 0px;
  font-size: 12px;
  padding: 5px 0px 5px 0px;
  line-height: 20px;
  color: ${Colors.lightGrey};
`;

const Title = styled.p`
  ${Mixins.helvetica}
  margin: 0px;
  font-size: 18px;
  font-weight: bold;
  padding: 0px 0px 5px 0px;
  color: ${Colors.black};
`;

const Label = styled.p`
  margin: 0px;
  font-size: 14px;
  font-weight: bold;
  padding-top: 10px;
`;

const LabelContainer = styled.div`
  display: flex;
  flex-direction: row;
`;

const InnerLabelContainer = styled.div`
  display: flex;
  flex-direction: column;
  margin-right: 15px;
`;

// const ReOrderButton = styled.a`
//   width: 60%;
//   background-color: ${Colors.backgroundDark};
//   height: 40px;
//   ${Mixins.helvetica};
//   margin-top: 5px;
//   padding: 5px;
//   line-height: 35px;
//   color: ${Colors.lightGrey2};
//   text-align: center;
// `;

const ReOrderButton = styled(ButtonBase)`
  &.MuiButtonBase {
    &-root {
      ${Mixins.helvetica}
      text-align: center;
      color: ${Colors.white};
      font-size: 16px;
      line-height: 52px;
      height: 52px;
      text-align: center;
      font-weight: 600;
      background: ${Colors.black};
      width: 70%;
      border-radius: 26px;
      letter-spacing: 0.48px;
      display: block;
      margin-top: 5px;

      &.Mui-disabled {
        background-color: ${Colors.disabledButton};
      }
    }
  }
`;

const Footer = styled.div`
  border-top: 1px solid black;
  display: flex;
  flex-direction: row;
  justify-content: center;
`;
