import React from 'react';
import { Link } from 'react-router-dom';

import styled from 'styled-components';

import { Colors, Mixins } from '../shared/typography';

function Item(props) {
  const {
    numberOfItems,
    name,
    shippingDays,
    code,
    image,
    id
  } = props;

  return (
    <Container>
      <UpperSection>
        <Image src={image} alt="replacement" />
        <Info>
          <NormalText>
            {numberOfItems}
            Items in Stock
          </NormalText>
          <Title>{name}</Title>
          <LabelContainer>
            <InnerLabelContainer>
              <Label>Shipping</Label>
              <NormalText>
                {shippingDays}
                bussines days
              </NormalText>
            </InnerLabelContainer>
            <InnerLabelContainer>
              <Label>EAN/UPC</Label>
              <NormalText>{code}</NormalText>
            </InnerLabelContainer>
          </LabelContainer>
        </Info>
      </UpperSection>
      <LowerSection>
        <LinkButton to={`/result/${id}`}>View Product</LinkButton>
      </LowerSection>
    </Container>
  );
}

export default Item;

const Container = styled.div`
  ${Mixins.helvetica};
  background-color: ${Colors.backgroundLighter};
  padding: 0px 15px 0px 15px;
  display: flex;
  flex-direction: column;
  height: 100%;
  box-sizing: border-box;
  margin-top: 10px;
  border: 1px solid ${Colors.backgroundLight};
  border-radius: 5px;
  box-shadow: 5px 0px 2px 0px #344E56;
`;

const UpperSection = styled.div`
  padding: 20px 0px 0px 0px;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
`;

const Info = styled.div`
  justify-content: end;
`;

const Image = styled.img`
  width: 90px;
  height: 90px;
  justify-content: start;
  margin-right: 20px;
  border: 1px solid black;
`;

const LinkButton = styled(Link)`
  ${Mixins.helvetica};
  Color: ${Colors.black};
  margin-bottom: 5px;
`;

const NormalText = styled.p`
  margin: 0px;
  font-size: 12px;
  padding-bottom: 5px;
  line-height: 20px;
  color: ${Colors.lightGrey};
`;

const Title = styled.p`
  ${Mixins.helvetica}
  margin: 0px;
  font-size: 18px;
  font-weight: bold;
  padding: 0px 0px 5px 0px;
  color: ${Colors.black};
`;

const Label = styled.p`
  margin: 0px;
  font-size: 14px;
  font-weight: bold;
  padding-top: 10px;
`;

const LabelContainer = styled.div`
  display: flex;
  flex-direction: row;
`;

const InnerLabelContainer = styled.div`
  display: flex;
  flex-direction: column;
  margin-right: 15px;
`;

const LowerSection = styled.div`
  display:flex;
  flex-direction: column;
  box-sizing: border-box;
  align-self: center;
`;
