import React from 'react';
import styled from 'styled-components';
import BuildIcon from '@material-ui/icons/Build';
import MenuBookIcon from '@material-ui/icons/MenuBook';
import { Colors, Mixins } from '../shared/typography';

export function AdditionalProductInfo() {
  return (
    <div>
      <Label> Additional Product Information </Label>
      <Link target="_blank" rel="noreferrer" href={`${process.env.PUBLIC_URL}/instruction_pdf.pdf`}>
        <InstructionIcon>
          <BuildIcon />
        </InstructionIcon>
        Instruction
      </Link>
      <Link target="_blank" rel="noreferrer" href={`${process.env.PUBLIC_URL}/warranty_pdf.pdf`}>
        <WarrantyIcon>
          <MenuBookIcon />
        </WarrantyIcon>
        Warranty
      </Link>
    </div>
  );
}
const Label = styled.p`
  margin: 0px;
  font-size: 14px;
  font-weight: bold;
  padding-top: 10px;
`;

const Link = styled.a`
  width: 100%;
  display: grid;
  background-color: ${Colors.backgroundLighter};
  grid-template-columns: 1fr 5fr;
  height: 40px;
  ${Mixins.helvetica};
  margin-top: 5px;
  padding: 5px;
  line-height: 35px;
  border-radius: 5px;
  color: ${Colors.backgroundDark};
`;

const InstructionIcon = styled(MenuBookIcon)`
  color: ${Colors.backgroundDark};
  padding:5px
`;

const WarrantyIcon = styled(BuildIcon)`
  color: ${Colors.backgroundDark};
  padding:5px
`;
