import React, { useState, useEffect } from 'react';
import {
  Switch,
  Route,
  Redirect,
} from 'react-router-dom';

import { Layout, HomePage } from '@scandit/react-toolkit';
import { ScanPage } from './components/ScanPage';
import { ResultsPage } from './components/ResultsPage';
import { HomeFooter as Footer } from './components/HomeFooter';
import { ResultsContext } from './shared/context';

const homeParams = {
  title: 'Re-ordering consumer/industrial products Scanning Demo',
  description: 'Scan the barcode of an item to show product information and re-order',
  scanText: 'Start Scanning',
  scanLink: '/scan-consumer-product',
  Footer
};

function AppContent() {
  const [productData, setProductData] = useState(null);
  useEffect(() => {
  }, [productData]);

  return (
    <ResultsContext.Provider value={{ productData, setProductData }}>
      <Layout>
        <Switch>
          <Route exact path="/">
            <HomePage {...homeParams} />
          </Route>
          <Route path="/home">
            <HomePage {...homeParams} />
          </Route>
          <Route path="/scan-consumer-product">
            <ScanPage />
          </Route>
          <Route path="/result/:id">
            <ResultsPage />
          </Route>
          <Redirect to="/" />
        </Switch>
      </Layout>
    </ResultsContext.Provider>
  );
}

export default AppContent;
